export default {
  name: "LayoutDefault",
  data() {
    return {
      leftDrawerOpen: false,
      navigationItems: [
        {
          icon: "home",
          label: "Cards",
          caption: "Cards List",
          toRouteName: "Cards"
        },
        {
          icon: "add",
          label: "AddCard",
          caption: "Add more cards",
          toRouteName: "AddCard"
        },
        {
          icon: "label",
          label: "Tags",
          caption: "Manage your tags",
          toRouteName: "Tags"
        },
        {
          icon: "format_align_left",
          label: "Words List",
          caption: "All stored words as a list",
          toRouteName: "WordsList"
        },
        {
          icon: "webhook",
          label: "Resources",
          caption: "Resources used in this app",
          toRouteName: "Resources"
        }
      ]
    };
  },
  computed: {
    title() {
      return this.$route.name
        ? this.$route.name.replace(/([A-Z])/g, " $1").trim()
        : "Word cards";
    },
    isLoggedIn() {
      return this.$store.getters.isLoggedIn;
    },
    user() {
      return this.$store.state.user;
    }
  },
  methods: {
    navigateTo(routeName) {
      if (this.$route.name !== routeName)
        this.$router.push({ name: routeName });
      else {
        this.leftDrawerOpen = false;
      }
    },
    logOut() {
      this.$q
        .dialog({
          title: "Log Out",
          message: "Are you sure you want log out?",
          cancel: true,
          persistent: true
        })
        .onOk(() => {
          this.$store.dispatch("logOut");
          this.$router.push({ name: "Home" });
        });
    }
  }
};
