export default {
  name: "Home",
  beforeCreate() {
    if (this.$store.getters.isLoggedIn) {
      this.$router.push({ name: "Cards" });
    }
  },
  computed: {
    showNoopLogin() {
      return process.env.NODE_ENV === "development";
    }
  },
  methods: {
    async noopLogin() {
      await this.$store.dispatch("noopLogin");
      this.$router.push({ name: "Cards" });
    }
  }
};
